import { createRouter, createWebHistory } from 'vue-router'
// import store from '../store/store.js'

import Login from '../views/Login.vue'
import Home from '../views/Home.vue'


// 메뉴별 기본 페이지
import Product from '../views/product/Product.vue'
import Cartridge from '../views/cartridge/Cartridge.vue'
import System from '../views/system/System.vue'
import ChangeHistory from '../views/changeHistory/ChangeHistory.vue'
import Firmware from '../views/firmware/Firmware.vue'
import License from '../views/license/License.vue'
import LicenseGroup from '../views/licenseGroup/LicenseGroup.vue'
import Agency from '../views/agency/Agency.vue'
import AgencyCode from '../views/agencyCode/AgencyCode.vue'
import AgencyCodeChange from '../views/agencyCodeChange/AgencyCodeChange.vue'
import ProductDate from '../views/productDate/ProductDate.vue'
import SerialNumber from '../views/serialNumber/SerialNumber.vue'
import AgencyInfo from '../views/agencyInfo/AgencyInfo.vue'
import User from '../views/user/User.vue'
import Part from '../views/part/Part.vue'
import Mail from '../views/mail/Mail.vue'
import SettingPart from '../views/setting/SettingPart.vue'
import SettingProduct from '../views/setting/SettingProduct.vue'
import SettingCountry from '../views/setting/SettingCountry.vue'
import SettingInfo from '../views/setting/SettingInfo.vue'

// 메뉴별 상세보기
import ProductDetail from '../views/product/ProductDetail.vue'
import FirmwareDetail from '../views/firmware/FirmwareDetail.vue'
import LicenseGroupDetail from '../views/licenseGroup/LicenseGroupDetail.vue'
import AgencyCodeDetail from '../views/agencyCode/AgencyCodeDetail.vue'
import UserDetail from '../views/user/UserDetail.vue'
import PartDetail from '../views/part/PartDetail.vue'
import LicenseDetail from '../views/license/LicenseDetail.vue'
import SerialNumberDetail from '../views/serialNumber/SerialNumberDetail.vue'
import ProductDateDetail from '../views/productDate/ProductDateDetail.vue'
import AgencyDetail from '../views/agency/AgencyDetail.vue'

// 메뉴별 기타 페이지
import LicenseKey from '../views/license/LicenseKey.vue'

const routes = [
  {
    path: "/login",
    name: "login",
    component: Login,
    beforeEnter: (to, from, next) => {
      // 토큰 정보(로그인 유무)
      let token = localStorage.getItem('token')
      if (token == null) {
        next()
      } else {
        next('/product')
      }
    },
  },
  {
    path: '/',
    name: 'home',
    component: Home,
    children: [
      {path: 'product', component: Product},                    // 제품 관리
      {path: 'cartridge', component: Cartridge},                // 카트리지/팁 로그
      {path: 'system', component: System},                      // 시스템 로그
      {path: 'changeHistory', component: ChangeHistory},        // 변경 이력 로그
      {path: 'firmware', component: Firmware},                  // 펌웨어 그룹 관리
      {path: 'licenseGroup', component: LicenseGroup},          // 라이선스 그룹 관리
      {path: 'license', component: License},                    // 라이선스 관리
      {path: 'agencyCode', component: AgencyCode},              // 대리점 코드 관리
      {path: 'agency', component: Agency},                      // 대리점 관리
      {path: 'agencyCodeChange', component: AgencyCodeChange},  // 대리점 코드 변경
      {path: 'agencyInfo', component: AgencyInfo},              // 대리점 정보 관리
      {path: 'productDate', component: ProductDate},            // 제품 날짜 변경
      {path: 'serialNumber', component: SerialNumber},          // 일련 번호 변경
      {path: 'user', component: User},                          // 사용자 관리
      {path: 'part', component: Part},                          // 부품 관리
      {path: 'mail', component: Mail},                          // 메일 알림 설정
      {path: 'settingPart', component: SettingPart},            // 부품 목록 관리
      {path: 'settingProduct', component: SettingProduct},      // 제품 목록 관리
      {path: 'settingCountry', component: SettingCountry},      // 국가 목록 관리
      {path: 'settingInfo', component: SettingInfo},            // 내 정보 관리
      // {path: '/:pathMatch(.*)*', component: Error},                    // 404 처리

      // 상세보기
      {path: 'productDetail', component: ProductDetail},                // 제품 관리 상세보기
      {path: 'firmwareDetail', component: FirmwareDetail},              // 펌웨어 그룹 관리 상세보기
      {path: 'licenseGroupDetail', component: LicenseGroupDetail},      // 라이선스 그룹 관리 상세보기
      {path: 'agencyCodeDetail', component: AgencyCodeDetail},          // 대리점 코드 관리 상세보기
      {path: 'userDetail', component: UserDetail},                      // 사용자 관리 상세보기
      {path: 'partDetail', component: PartDetail},                      // 부품 관리 상세보기
      {path: 'licenseDetail', component: LicenseDetail},                // 라이선스 관리 상세보기
      {path: 'serialNumberDetail', component: SerialNumberDetail},      // 일련 번호 변경 상세보기
      {path: 'productDateDetail', component: ProductDateDetail},        // 제품 날짜 변경 상세보기
      {path: 'agencyDetail', component: AgencyDetail},                  // 대리점 관리 상세보기
      
      // 메뉴별 기타 페이지
      {path: 'licenseKey', component: LicenseKey},                    // 라이선스 키 생성

    ],
    beforeEnter: (to, from, next) => {
      // 토큰 정보(로그인 유무)
      let token = localStorage.getItem('token')
      if (token == null) {
        next('/login')
      } else {
        let currentMenu = to.path.replace('/','');
        currentMenu = currentMenu.replace('Detail','');
        console.log(currentMenu)
        localStorage.setItem("currentMenu", currentMenu)
        if(to.path == '/'){
          next('/product')
        } else {
          next()
        }
        
      }
    }
  },
]

// const requireAuth = () => (from, to, next) => {
//   const isAuthenticated = false
//   if (isAuthenticated) return next()
//   next('/login?returnPath=me')
// }

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
