<template>
    <div class="contents">
        <!-- 상세검색 start -->
        <div class="search-panel" :class="{'search-panel-open': searchPanelOn}">
            <div class="bg"></div>
            <div class="inner scroll-y">
                <div class="list-area">
                    <ul class="input-txt-list">
                        <li class="tit">대리점명</li>
                        <li class="cont">
                            <input type="text">
                        </li>
                    </ul>
                    <ul class="input-txt-list">
                        <li class="tit">이름</li>
                        <li class="cont">
                            <input type="text">
                        </li>
                    </ul>
                    <ul class="input-txt-list">
                        <li class="tit">아이디</li>
                        <li class="cont">
                            <input type="text">
                        </li>
                    </ul>
                </div>    
                <div class="btn-box two">
                    <button class="btn-sb">초기화</button>
                    <button class="btn">검색</button>
                </div>
            </div>
            <button @click="searchPanelOn = !searchPanelOn" class="btn-fold"></button>
        </div>
        <!-- 상세검색 end -->
        <h2 class="nav-title">
            <p>
                <a href="" class="now">사용자 관리</a>
            </p>
        </h2>
        <div class="inner">
            <div class="scroll-y">
                <div class="top-bar">
                    <div class="btn-box multi">
                        <button class="btn-sb-g">엑셀 다운로드</button>
                    </div>
                    <div class="btn-box tab">
                        <button @click="searchPanelOn = !searchPanelOn" class="btn-sb-g">상세 검색</button>
                    </div>
                </div>
                <div class="board user">
                    <ul class="list-tit">
                        <li v-for="(column) in columnData" :key="column">{{column.korName}}</li>
                    </ul>
                    <ul v-for="(row) in rowData" :key="row" class="list-item">
                        <li>
                            <ul class="item">
                                <li @click="moveDetail" v-for="(column) in columnData" :key="column">{{row[column.engName]}}</li>
                            </ul>
                        </li>
                    </ul>
                </div>
                <Pagination :rowData="rowData"/>
            </div>
        </div>
    </div>
</template>

<script>
import { getData } from '../../js/data.js';
export default {
    name: 'User',
    data() {
        return {
            searchPanelOn: false,
            columnData: null,
            rowData: [],
        }
    },
    beforeMount() {
        let gridData = getData('user');
        this.columnData = gridData.columnDefs
        this.rowData = gridData.rowData
    },
    methods: {
        moveDetail() {
            this.$router.push('/userDetail')
        }
    },
}
</script>

