                        <li class="cont">
                            <input type="checkbox" name="" id="serverYn" class="none">
                            <label for="serverYn">서버 연동</label>
                        </li>
                    </ul>
                </div>    
                <div class="btn-box two">
                    <button class="btn-sb" @click="refresh">초기화</button>
                    <button class="btn" @click="search">검색</button>
