<script>
import { getData } from '../../js/data.js';
export default {
    name: 'LicenseGroup',
    data() {
        return {
            searchPanelOn: false,
            columnData: null,
            rowData: [],
        }
    },
    beforeMount() {
        let gridData = getData('licenseGroup');
        this.columnData = gridData.columnDefs
        this.rowData = gridData.rowData
    },
    methods: {
        moveDetail() {
            this.$router.push('/licenseGroupDetail')
        }
    },
}
</script>

