<template>
    <div class="contents">
        <h2 class="nav-title">
            <p>
                <a href="" class="now">라이선스 관리</a>
            </p>
        </h2>
        <div class="inner">
            <div class="scroll-y">
                <h3>라이선스 키 생성</h3>
                <div class="box">
                    <div class="inner">
                        <ul class="input-txt-list">
                            <li class="tit">일련 번호</li>
                            <li class="cont">
                                <input type="text" placeholder="">
                            </li>
                            <li class="tit">제품명</li>
                            <li class="cont">
                                <input type="text" placeholder="">
                            </li>
                            <li class="tit">Request Code</li>
                            <li class="cont">
                                <input type="text" placeholder="">
                            </li>
                        </ul>
                        <div class="btn-box full">
                            <button @click="licenseKey" class="btn">확인</button>
                        </div>
                    </div>
                </div>
                <h3>라이선스 변경 내역</h3>
                <div class="board license">
                    <ul class="list-tit">
                        <li v-for="(column) in columnData" :key="column">{{column.korName}}</li>
                    </ul>
                    <ul v-for="(row) in rowData" :key="row" class="list-item">
                        <li>
                            <ul class="item">
                                <li @click="moveDetail" v-for="(column) in columnData" :key="column">{{row[column.engName]}}</li>
                            </ul>
                        </li>
                    </ul>
                </div>
                <Pagination :rowData="rowData"/>
            </div>
        </div>
    </div>
</template>

<script>
import { getData } from '../../js/data.js';
export default {
    name: 'License',
    data() {
        return {
            searchPanelOn: false,
            columnData: null,
            rowData: [],
        }
    },
    beforeMount() {
        let gridData = getData('license');
        this.columnData = gridData.columnDefs
        this.rowData = gridData.rowData
    },
    methods: {
        moveDetail() {
            this.$router.push('/licenseDetail')
        },
        licenseKey() {
            this.$router.push('/licenseKey')
        },
    },
}
</script>

