<template>
    <!-- PC start -->
    <nav class="pc">
        <h1>
            <a href="/"></a>
        </h1>
        <ul class="gnb scroll-y">
            <li v-for="(menu) in menus" :key="menu" @click="moveMenu(menu.path, menu.className)" :class="{'active': currentMenu == menu.className}">
                <i class="fa-solid fa-list-check"></i>
                <a>{{menu.name}}</a>
                <ul class="lnb" v-if="menu.id == 'setting'">
                    <li v-for="(setting) in menu.child" :key="setting"
                    @click="moveSettingMenu(setting.path, setting.className)" :class="{'active': settingMenu == setting.className}">
                        <a>{{setting.name}}</a>
                    </li>
                </ul>
            </li>
        </ul>
    </nav>
    <!-- PC end-->
    <!-- MOBILE start -->
    <nav class="mobile" :class="{'mobile-open': mobileOpen}">
        <div class="user-menu mobile">
            <button @click="mobileClose" class="close"></button>
            <div>
                <div class="alert">
                    <a class="" href=""></a>
                </div>
                <button class="info">
                    <div class="user">
                        <span>{{name}}</span>
                        <span>님1</span>
                    </div>
                    <ul class="info-list">
                        <li class="id">
                            <span>ID :</span>
                            <span>{{id}}</span>
                        </li>
                        <li>
                            <a href="">내 정보</a>
                        </li>
                        <li @click="doLogout">
                            <a @click="doLogout">로그아웃</a>
                        </li>
                    </ul>
                </button>
            </div>
        </div>
        <ul class="gnb scroll-y">
            <li v-for="(menu) in menus" :key="menu" @click="moveMenu(menu.path, menu.className)" :class="{'active': currentMenu == menu.className}">
                <i class="fa-solid fa-list-check"></i>
                <a>{{menu.name}}</a>
                <ul class="lnb" v-if="menu.id == 'setting'">
                    <li v-for="(setting) in menu.child" :key="setting"
                    @click="moveSettingMenu(setting.path, setting.className)" :class="{'active': settingMenu == setting.className}">
                        <a>{{setting.name}}</a>
                    </li>
                </ul>
            </li>
        </ul>
    </nav>
    <!-- MOBILE end -->
</template>

<script>
export default {
    name: 'Menu',
    data() {
        return {
            menus: JSON.parse(localStorage.getItem("menus")),
            currentMenu: localStorage.getItem("currentMenu"),
            settingMenu: '',
            name: localStorage.getItem('name'),
            id: localStorage.getItem('id'),
            mobileOpen: false,
        }
    },
    methods: {
        moveMenu(path, menu) {
            if(path != '/settingPart' && path != '/settingProduct' && path != '/settingCountry' && path != '/settingInfo') {
                this.currentMenu = menu
                this.$router.push(path)
            }
        },
        moveSettingMenu(path, menu) {
            this.currentMenu = 'setting'
            this.settingMenu = menu
            this.$router.push(path)
        },
        mobileClose() {
            this.emitter.emit('mobileOpen', false)
        }
    },
    created() {
        console.log("menu created");
        console.log(this.currentMenu);

        this.emitter.on('mobileOpen', data=>{
            this.mobileOpen = data
        })
    },
}
</script>

