<template>
    <div class="page-box">
        <div class="page-nation">
            <!-- <a class="arrow pprev" href="#"></a> -->
            <a @click="prev" class="arrow prev" href="#"></a>
            <a v-for="(pageNum) in pageList" :key="pageNum" href="#"
            :class="{'active': pageNum == pageNo}">{{pageNum}}</a>
            <!-- <a href="#" class="active">1</a>
            <a href="#">2</a>
            <a href="#">3</a>
            <a href="#">4</a>
            <a href="#">5</a>
            <a href="#">6</a>
            <a href="#">7</a>
            <a href="#">8</a>
            <a href="#">9</a>
            <a href="#">10</a> -->
            <a @click="next" class="arrow next" href="#"></a>
            <!-- <a class="arrow nnext" href="#"></a> -->
        </div>
    </div>
</template>

<script>
export default {
    name: 'Pagination',
    data() {
        return {
            pageCountOfGroup: 5,
            pageNo: 1,
            pageRowSize: 5,
            totalCount: 0,
            
            totalPageNo: 0,
            pageGroup: 0,
            endPageGroup: 0,

            startPageOfGroup: 0,
            endPageOfGroup: 0,

            startRowNum: 0,
            endRowNum: 0,

            pageList: [],
        }
    },
    methods: {
        prev() {
            this.pageNo = this.startPageOfGroup - 1
        },
        next() {
            this.pageNo = this.endPageOfGroup + 1
        }
    },
    props: {
        rowData: Array,
    },
    mounted() {
        // console.log("paging mounted")
        // console.log(this.rowData)
        // console.log(this.rowData.length)
        this.totalCount = this.rowData.length

        this.totalPageNo = Math.ceil(this.totalCount / this.pageRowSize)

        this.pageGroup = Math.ceil(this.pageNo / this.pageCountOfGroup)
        this.endPageGroup = Math.ceil(this.totalPageNo / this.pageCountOfGroup)

        this.startPageOfGroup = (this.pageGroup - 1) * this.pageCountOfGroup + 1
        this.endPageOfGroup = this.pageGroup * this.pageCountOfGroup
        this.endPageOfGroup = this.endPageOfGroup > this.totalPageNo ? this.totalPageNo : this.endPageOfGroup

        this.startRowNum = (this.pageNo - 1) * this.pageRowSize + 1;
        this.endRowNum = this.pageNo * this.pageRowSize;
        this.endRowNum = this.endRowNum > this.totalCount ? this.totalCount : this.endRowNum

        console.log("totalPageNo");
        console.log(this.totalPageNo);
        console.log("pageGroup");
        console.log(this.pageGroup);
        console.log("endPageGroup");
        console.log(this.endPageGroup);
        console.log("startPageOfGroup");
        console.log(this.startPageOfGroup);
        console.log("endPageOfGroup");
        console.log(this.endPageOfGroup);
        console.log("startRowNum");
        console.log(this.startRowNum);
        console.log("endRowNum");
        console.log(this.endRowNum);

        for(let i = this.startPageOfGroup; i<= this.endPageOfGroup; i++) {
            this.pageList.push(i)
        }
        // this.pageList
    },
}
</script>

