function getMenu(value) {
    //let auth = localStorage.getItem("auth");
    
    let returnMenu = new Array()

    menuAuth[value].forEach((r)=>{
        // menu.r
        returnMenu.push(menu[r])
    })

    return returnMenu
}

// export default getMenu(value)
export {getMenu}