<template>
  <div class="wrap">
    <div class="login-box pos-center">
        <div class="logo">
            <h1></h1>
        </div>
        <div class="info">
            <div>
                <div class="tit">로그인</div>
                <ul class="input-txt-list">
                    <li class="cont">
                        <input v-model="userData.userId" type="text" placeholder="아이디">
                    </li>
                    <li class="cont">
                        <input v-model="userData.userPw" type="text" placeholder="비밀번호">
                    </li>
                </ul>
                <div class="chk-box">
                    <input type="checkbox" id="keep" class="none">
                    <label for="keep" class="chk">로그인 유지</label>
                </div>
                <div class="btn-box full">
                    <button @click="doLogin" class="btn">로그인</button>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'Login',
    data() {
        return {
            userData: {
                userId: '',
                userPw: ''
            }
        }
    },
    methods: {
        doLogin() {
            this.$store.dispatch('doLogin', this.userData).then(() => {
                this.userId = ''
                this.userPw = ''
            })
        },
    },
}
</script>

