<template>
    <div class="snb">
        <div class="inner">
            <h1 class="mobile">
                <a href=""></a>
            </h1>
            <div>
                <div class="user-menu pc">
                    <div class="alert">
                        <a class="" href=""></a>
                    </div>
                    <button class="info" @click="toggle = !toggle" v-click-outside="closeDom">
                        <div class="user">
                            <span>{{name}}</span>
                            <span>님2</span>
                        </div>
                        <ul v-if="toggle" class="info-list">
                            <li class="id">
                                <span>ID :</span>
                                <span>{{id}}</span>
                            </li>
                            <li>
                                <a>내 정보</a>
                            </li>
                            <li @click="doLogout">
                                <a @click="doLogout">로그아웃</a>
                            </li>
                        </ul>
                    </button>
                </div>
                <div class="select-box">
                    <select class="gray" name="" id="">
                        <option value="">한국기준(UTF-9)</option>
                        <option value="">사용국가기준(UTF-?)</option>
                        <option value="">서버기준(UTF-0)</option>
                    </select>
                </div>
                <button @click="openMobileMenu" class="hamburger"></button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Header',
    data() {
        return {
            toggle: false,
            name: localStorage.getItem('name'),
            id: localStorage.getItem('id'),
        }
    },
    methods: {
        doLogout() {    // 로그아웃
            this.$store.dispatch('doLogout').then(() => {})
        },
        closeDom() {    // dom 외부 클릭
            if(this.toggle) {
                this.toggle = false
            }
        },
        openMobileMenu() {
            this.emitter.emit('mobileOpen', true)
        }
    },
}
</script>

