import router from '@/router/router'
import axios from 'axios'
import { createStore } from 'vuex'
import { getMenu } from '../js/menu.js';
import { getData } from '../js/data.js';
import cartridgeModule from './modules/cartridgeModule.js'

const store = createStore({
  modules: {
    cartridgeModule,
  },
  state:{
    token: null,
    auth: null,
    gridData: null,
  },
  // getters:{
  //   checkToken: (state)=>{
  //     if(state.token!=null) return true
  //     else return false
  //   }
  // },
  mutations:{
    LOGIN_MODULE(state,response){
      console.log("로그인");
      if(response.resultCode > 399){
        alert("로그인 정보가 올바르지 않습니다.")
        state.token = null
        state.auth = null
        localStorage.removeItem('token')
        
      }else{
        localStorage.setItem('token', response.result.token)
        localStorage.setItem('id', "admin");
        localStorage.setItem('name', "관리자");


        let menus = getMenu('admin');
        localStorage.setItem('menus', JSON.stringify(menus));
        
        // state.auth = response.result.auth
        axios.defaults.headers.common['Authorization']=`${state.token}`
        

        let gridData = getData('product');
        state.gridData = gridData;
        
        //권한에 따라 메인 페이지 설정
        router.push("/product")
      }
    },
    LOGOUT_MODULE(state) {
      localStorage.removeItem("token");
      localStorage.removeItem("id");
      localStorage.removeItem("name");
      localStorage.removeItem("menus");
      state.token = null;
      state.auth = null;
      router.push("/login");
    },
    // GET_DATA(state, data) {
    //   let gridData = getData(data.id);
    //   state.gridData = gridData;
    //   router.push(data.path)
    // },



    LOGIN_MODULE_TEST(state) {
      console.log("로그인 임시");
      localStorage.setItem('token', 'aaaaabbbbbbccccc')
      localStorage.setItem('id', "admin");
      localStorage.setItem('name', "관리자");


      let menus = getMenu('admin');
      localStorage.setItem('menus', JSON.stringify(menus));
      
      // state.auth = response.result.auth
      // axios.defaults.headers.common['Authorization']=`${state.token}`
      

      let gridData = getData('product');
      state.gridData = gridData;
      
      //권한에 따라 메인 페이지 설정
      router.push("/product")
    }
  },
  actions:{
    // doLogin({commit},data){
    //   console.log(111);
    //   return axios.post('/generateToken',data).then(response=>commit('LOGIN_MODULE',response.data))
    // },
    doLogout(context) {
      context.commit('LOGOUT_MODULE');
    },
    doLogin(context) {
      context.commit('LOGIN_MODULE_TEST');
    },
  }
})

export default store